body{
    background-image: url('./img/gradient.png');
    background-size: contain;
    font-family: 'Prompt', sans-serif;
}

.movieComponentVertical{
    margin: 0 8rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}

.movieComponentHorizontal{
    margin-left: 26rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.buscador{
    padding-left: 20rem;
}

.movieItem{
    margin: 2rem;
    padding: 2rem;
    border-radius: 0.5rem;
    color: white;
    background-color: rgba(255, 255, 255, 0.2);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
}

.movieItem p{
    text-align: justify;
    width: 30rem;
}

.poster{
    width: 40%;
}

.hide{
    display: none;
}

.show{
    display: block;
}

.header {
    text-align: center;
}

.header img{
    width: 8rem;
    margin: 4rem;
    vertical-align: middle;
    background-color: rgba(255, 255, 255, 0.2);
    padding: 0.5rem;
    border-radius: 0.5rem;
}

.footer{
    display: flex;
    justify-content: space-around;
    text-align: center;
    margin: 2rem;
}

.footer p{
    background-color: rgba(0, 0, 0, 0.2);
    color: white;
    padding: 0.5rem;
    border-radius: 0.5rem;
}
.agregar{
    color: #FE8CDF;
    background: #353866;
    border-radius: 8px;
    width: 130px;
    padding: 11px;
    cursor: pointer;
    border: none;
  }
.view{
    color: #FE8CDF;
    background: #353866;
    border-radius: 8px;
    width: 130px;
    padding: 11px;
    cursor: pointer;
    border: none;
}
.delete{
    color: white;
    background: rgb(39, 38, 38);
    border-radius: 8px;
    width: 100px;
    padding: 4px;
    cursor: pointer;
    border: none;
}